import { fetchMovieDetails, fetchMovieReviews, fetchMovieVideos, fetchSimilarMovies } from './../api';

const MovieDetails = async (id, targetEl) => {
  const renderDetails = (data) => {
    const { videos, reviews, similarMovies } = data;
    console.log({ data });
    let videoHtml = '';
    let reviewsHtml = '';
    let similarMoviesHtml = '';

    const trailer = videos.find(v => v.type === 'Trailer');
    if (trailer) {
      const { key } = trailer;
      videoHtml = `
        <iframe id="ytplayer" type="text/html" width="640" height="360"
          src="https://www.youtube.com/embed/${key}?autoplay=1"
          frameborder="0"></iframe>`;
    }

    if (reviews && reviews.length > 0) {
      reviewsHtml = `
        <div class="movie-details__reviews">
          <h4>Reviews</h4>
          ${reviews.slice(0, 2).map(review => {
            const {author, content} = review;
            return `
              <div class="movie-details__review-item">
                <span class="movie-details__review-author">by ${author}:</span>
                <p class="movie-details__review-content">${content}</p>
              </div>
            `
          }).join('')}
         </div>
       `;
    }

    if (similarMovies && similarMovies.length > 0) {
      similarMoviesHtml = `
      <div class="movie-details__similar-movies">
        <h4>Similar Movies</h4>
        ${similarMovies.map(movie => movie.title).join(', ')}.
      </div>`
    }

    const html = ` 
        ${videoHtml}
        ${similarMoviesHtml}
        ${reviewsHtml}
    `;

    const detailsDiv = targetEl.getElementsByClassName('movie-list-item__details')[0];
    detailsDiv.innerHTML = html;

    setTimeout(() => {
      detailsDiv.classList.add('movie-list-item__details--show');
    }, 550);
  };

  const loadData = async () => {
    targetEl.getElementsByClassName('movie-list-item__details')[0].innerHTML = 'Loading...';
    return Promise.all([
      fetchMovieDetails(id),
      fetchMovieReviews(id),
      fetchMovieVideos(id),
      fetchSimilarMovies(id),
    ]).then(result => {
      return {
        details: result[0],
        reviews: result[1].results,
        videos: result[2].results,
        similarMovies: result[3].results,
      };
    });
  };

  await loadData().then(data => renderDetails(data));
};

export default MovieDetails;
